import React from 'react'
import Head from 'next/head'
import clsx from 'clsx'
import { useRouter } from 'next/router'

import { Footer } from './Footer'
import { FullPageLayout } from './layouts/FullPageLayout'
import { Link } from './Link'
import { Nav } from './Nav'
import { SidebarLayout } from './layouts/SidebarLayout'
import { VisuallyHidden } from '../library/VisuallyHidden'

export interface RootProps extends React.HTMLAttributes<HTMLDivElement> {
  description?: string
  title: string
}

export const Root: React.FC<RootProps> = ({
  className,
  children,
  description,
  title,
  ...rest
}) => {
  const { pathname } = useRouter()
  debugger;

  return (
    <div
      className={clsx('flex flex-column', className)}
      style={{ minHeight: '100vh' }}
      {...rest}
    >
      <VisuallyHidden as="a" focusable href="#main">
        Skip to content
      </VisuallyHidden>
      <Head>
        <link rel="icon" href="/favicon.ico" />
        <title>{title}</title>
        {!!description && <meta name="description" content={description} />}
      </Head>
      <header className="bb b--silver items-center flex" role="banner">
        <Link className="fw7 pv3" href="/">
          Hello, Next.js!
        </Link>
        <Nav
          className="pl3"
          inline
          links={[
            {
              href: '/getting-started',
              name: 'Getting Started'
            },
            {
              href: '/components',
              name: 'Components'
            }
          ]}
        />
      </header>
      <main id="main" style={{ flex: 2 }}>
        {pathname.includes('components') ? (
          <SidebarLayout
            sidebar={
              <Nav
                className="mv3"
                links={[
                  {
                    href: '/components/Alert',
                    name: 'Alert'
                  },
                  {
                    href: '/components/Breadcrumb',
                    name: 'Breadcrumb'
                  },
                  {
                    href: '/components/Button',
                    name: 'Button'
                  },
                  {
                    href: '/components/Card',
                    name: 'Card'
                  }
                ]}
              />
            }
          >
            {children}
          </SidebarLayout>
        ) : (
          <FullPageLayout>{children}</FullPageLayout>
        )}
      </main>
      <Footer />
    </div>
  )
}
